			swiperGridHeight: '0px',
			swiperGridWidth: '100%',
			data2: [{
					image: '/static/menu/menu2_1.png',
				},
				{
					image: '/static/menu/menu2_2.png',
				},
				{
					image: '/static/menu/menu2_3.png',
				},
				{
					image: '/static/menu/menu2_4.png',
